.header {
  height: 50px;
  width: 100%;
  background: #4a4c4d;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.2);
  color: #fff;
  z-index: 20;
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  height: 100%;

  display: -webkit-flex;

  display: flex;
}

.header-content__left {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  height: 100%;
}

.header-content__right {
  height: 100%;
}

.header-logo {
  height: 100%;
  line-height: 50px;
  margin-right: 50px;
  font-weight: 400;
}

.header-links {
  margin: 0;
  padding: 0;
  list-style: none;
  display: -webkit-flex;
  display: flex;
  height: 100%;
}

.header-links__item {
  line-height: 50px;
  display: block;
}

.header-links__item-link {
  line-height: 50px;
  display: block;
  background: none;
  color: #eee;
  text-decoration: none;
  height: 100%;
  width: 100%;
  font-weight: 600;
  padding: 0 20px;
  border-top: none;
  border-right: none;
  border-bottom: 0px solid #eee;
  border-left: none;
  box-sizing: border-box;
  -webkit-transition: border-width 200ms,
    border-color 200ms,
    color 200ms;
  transition: border-width 200ms,
    border-color 200ms,
    color 200ms;
  cursor: pointer;
  outline: none;
}

.header-links__item:not(.header-links__item--active) .header-links__item-link:hover {
  border-bottom: 4px solid #eee;
  color: #fff;
}

.header-links__item--active .header-links__item-link {
  border-bottom: 4px solid hsl(173, 100%, 37%);
  color: hsl(173, 100%, 37%);
}

.header-links__item--active .header-links__item-link:hover {
  border-color: hsl(173, 100%, 45%);
  color: hsl(173, 100%, 45%);
}

.header-links__item--primary {
  padding: 7px 0;
  margin: 0 10px;
}

.header-links__item--primary .header-links__item-link {
  line-height: 36px;
  background: hsl(31, 69.4%, 51.4%);
  height: auto;
  border-radius: 2px;
  -webkit-transition: background 200ms;
  transition: background 200ms;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  color: #fff;
}

.header-links__item.header-links__item--primary .header-links__item-link:hover {
  border-width: 0;
  background: hsl(31, 65%, 47%);
}

.header-links__item.header-links__item--primary a:active {
  background: hsl(31, 60%, 42%);
}

.matrix-tooltip {
  position: absolute;
  background: #4a4c4d;
  color: #fff;
  padding: 5px 10px;
  border-radius: 2px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  display: inline;
  -webkit-transform: translate(-50%, -100%);
          transform: translate(-50%, -100%);
  max-width: 250px;
  font-size: 0.92857em;
  pointer-events: none;
  z-index: 3;
}

.matrix-tooltip::after {
    content: " ";
    position: absolute;
    top: 100%; /* At the bottom of the tooltip */
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #4a4c4d transparent transparent transparent;
}

